.headless-app {
    padding: 20px;
}

.headless-app table {
    border-collapse: collapse;
}

.headless-app table, .headless-app th, .headless-app td {
    border: 1px solid black;
}

.headless-app tbody td {
    text-align: right;
    padding-right: 10px;
}

.headless-app button {
    width: 100%;
    min-width: 500px;
}
